@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap);
body{
  margin: 0;
  padding: 0;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Bai Jamjuree', sans-serif;
}


p{
  line-height: 1.5em;
}


@media (max-width: 820px){

.container{
  width: 100%;
  padding: 2em;
}

}

.Header{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    padding-top: 5em;
    background-image: url(/clip/static/media/bg-header-desktop.df6601b0.png);
    background-size: cover;
    background-repeat: no-repeat;
}

img{
    width: 80px;
    margin-bottom: 1.5em;
}


h2{
    font-weight: 600;
    font-size: 2.5rem;
    margin: 0;
}

p{
    max-width: 35em;
    font-size: 1.2rem;
}

.header-article{
    margin-top: 4em;
}

.header-article h2{
    font-size: 2rem;
}

@media (max-width: 820px){
    
    .Header{
    width: 100%;
    background-image: url(/clip/static/media/bg-header-mobile.80627651.png);
    background-size: cover;
    background-repeat: no-repeat;
    }
  


}

.button-contianer{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
}

button{
    padding: 1em 2em;
    border-radius: 2rem;
    border: none;
    margin-bottom: 2em;
    color: white;
    cursor: pointer;
}

button:hover{
    color: black;
    background-color: white;
}


.ios{
    background-color: hsl(140, 66%, 44%);
}

.mac{
    background-color: hsl(233, 100%, 69%);
}
.main-top-section{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 3em;
    grid-gap: 5em;
    gap: 5em;
}

.image-container{
    width: 50%;
    max-width: 700px;
    min-width: 450px;
}

.image-container img{
    width: 100%;
    min-width: 450px;
    overflow: hidden;
}


.top-content{
    padding-top: 4em;
}

 .top-content h2{
    font-size: 1.5rem;
    font-weight: bold;
    color: black;
}

.top-content p{
    font-size: 1rem;
    max-width: 70%;
}



@media (max-width: 820px){

    .main-top-section{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0;
        gap: 0;
    }

    .image-container{
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        align-content: center;
    }

    .image-container img{
        width: 350px;
        max-width: 350px;
        min-width: 250px;
    }
   
    .sub-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 2em;
    }



    .top-content p{
        font-size: 1.25rem;
        min-width: 100%;
        text-align: center;
    }


    .top-content h2{
        padding-top: 0;
        margin-top: 0;
        font-size: 1.75rem;
    }

}
